<template>
  <div class="login-page d-flex align-items-center justify-content-center vh-100 bg-light">
    <div class="card shadow-lg p-4" style="width: 100%; max-width: 420px; border-radius: 1rem;">
      <div class="card-body">
        <div class="text-center mb-4">
          <div class="display-6 mb-2">üîê ÁÆ°ÁêÜÂì°ÁôªÂÖ•</div>
          <p class="text-muted">Ë´ãËº∏ÂÖ•Â∏≥ËôüËàáÂØÜÁ¢º</p>
        </div>
        <form @submit.prevent="signIn">
          <div class="mb-3">
            <label for="inputEmail" class="form-label">Email ‰ø°ÁÆ±</label>
            <input type="email" id="inputEmail" class="form-control"
              placeholder="admin@example.com" required autofocus v-model="user.username"
            />
          </div>
          <div class="mb-3">
            <label for="inputPassword" class="form-label">ÂØÜÁ¢º</label>
            <input type="password" id="inputPassword" class="form-control"
              placeholder="Ë´ãËº∏ÂÖ•ÂØÜÁ¢º" required v-model="user.password"
            />
          </div>
          <div class="d-grid mt-4">
            <router-link to="/" class="btn btn-secondary btn-lg" type="submit">ÂõûÂà∞È¶ñÈ†Å</router-link>
          </div>
          <div class="d-grid mt-4">
            <button class="btn btn-primary btn-lg" type="submit">ÁôªÂÖ•</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import ToastMessage from '@/mixins/ToastMessage'
export default {
  data () {
    return {
      user: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    signIn () {
      const api = `${process.env.VUE_APP_API}admin/signin`
      this.$http.post(api, this.user)
        .then((res) => {
          if (res.data.success) {
            const { token, expired } = res.data
            document.cookie = `hexToken=${token}; expires=${new Date(expired)}`
            this.$router.push('/dashboard/products')
          }
        })
        .catch((err) => {
          this.showToast('error', 'ÁôªÂÖ•Â§±ÊïóÔºåË´ãÁ¢∫Ë™çÂ∏≥ËôüÂØÜÁ¢ºÊòØÂê¶Ê≠£Á¢∫')
          console.error('ÁôªÂÖ•ÈåØË™§Ôºö', err)
        })
    }
  },
  mixins: [ToastMessage]
}
</script>

<style scoped>
.login-page {
  background: linear-gradient(135deg, #e0eafc, #cfdef3);
}
</style>
